        }
    }

    &.card-bg-light {
        background-color: $light;
        color: $default-text-color;

        .card-header {
            background-color: rgba(0, 0, 0, 0.05) !important;
            color: $default-text-color;
            border-block-end: $default-border;
        }

        .card-body {
