    }

    &.card-bg-dark {
        background-color: $dark;
        color: $custom-white;

        .card-header {
            background-color: rgba(255, 255, 255, 0.1) !important;
            color: $white;
            border-block-end: 1px solid rgba(255, 255, 255, 0.2);

            &:before {
                background-color: $custom-white;
            }
        }

        .card-body {
